import React from 'react'
import { updateTitle } from '../../App'
import './Contact.css'

export default function Contact() {
    updateTitle("Contact")
    return <>
    <div className="container pb-5">
        <div className="row py-5 mt-5">
            <h2 className=" fw-bold display-6 pt-5 text-uppercase dark-blue text-center">contact section</h2>
            <div className="portfolio text-center d-flex align-items-center justify-content-center">
            <i className="dark-blue fa-solid fa-star"></i>
            </div>
        </div>
        <div className="d-flex justify-content-center pt-4">
        <div className="w-50">
            <div className="pb-4">
                <input type="text" className="border-0 border-bottom rounded-3 w-100 ps-3" placeholder="Name"/>
            </div>  
            <div className="py-4">
                <input type="text" className="border-0 border-bottom rounded-3 w-100 ps-3" placeholder="Age"/>
            </div>  
            <div className="py-4">
                <input type="text" className="border-0 border-bottom rounded-3 w-100 ps-3" placeholder="Email"/>
            </div>  
            <div className="py-4">
                <input type="text" className="border-0 border-bottom rounded-3 w-100 ps-3" placeholder="Password"/>
            </div>  
            <button className="teal-bg border-0 rounded-2 py-1 px-2 text-white">Send Message</button>
        </div>
        </div>
    </div>
    </>
}
