import React, { useState } from 'react'
import Img from './port2.png'
import Img1 from './port3.png'
import Img2 from './poert1.png'
import { updateTitle } from '../../App'


export default function Portfolio() {
    const [selectedImage, setSelectedImage] = useState(null);
    function handleClick (image) {
        setSelectedImage(image);
      };
      function removeImg() {
        setSelectedImage(null);
      }
      updateTitle("Portfolio")
    return <>
    <div className="container pb-5">
        <div className="row pt-5 mt-5">
            <h2 className=" fw-bold display-6 pt-5 text-uppercase dark-blue text-center">Portfolio component</h2>
            <div className="portfolio text-center d-flex align-items-center justify-content-center">
            <i className="dark-blue fa-solid fa-star"></i>
            </div>
        </div>
        <div className="row gy-4">
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img2)}>
                     <img className="port-img rounded-3 w-100" src={Img2} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img)}>
                     <img className="port-img rounded-3 w-100" src={Img} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img1)}>
                     <img className="port-img rounded-3 w-100" src={Img1} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img2)}>
                     <img className="port-img rounded-3 w-100" src={Img2} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img)}>
                     <img className="port-img rounded-3 w-100" src={Img} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
            <div className="col-md-4">
                <div className="position-relative" onClick={()=>handleClick(Img1)}>
                     <img className="port-img rounded-3 w-100" src={Img1} alt="" />
                     <div className="rounded-3 position-absolute port-layer top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center">
                     <i className="text-white fa-solid fa-plus fa-7x"></i>
                </div>
                </div>     
            </div>
        </div>
        {selectedImage?<div className="overlay" onClick={removeImg}>
        <img src={selectedImage} alt="Selected Image"/>
        </div>:''}
    </div>
   
    </>
}

