import React from "react"
import { Link } from 'react-router-dom';

 
export default function Navbar() {
    return <>

<nav className="navbar navbar-expand-lg navbar-bg fixed-top">
  <div className="container">
  <Link className="navbar-brand py-4" to=""><h2 className="text-white fw-bold text-uppercase">Start Framework</h2></Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link about active text-white fw-medium pe-4 text-uppercase" to="about">About</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active text-white fw-medium pe-4 text-uppercase" to="portfolio">Portfolio</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active text-white fw-medium pe-4 text-uppercase" to="contact">Contact</Link>
        </li>
       
      </ul>
    </div>
  </div>
</nav>
    </>
}


